<template>
  <van-action-sheet
    title=""
    :value="value"
    @input="$emit('input',$event)"
    @closed="editing=false"
  >
    <div class="channel">
      <div class="tit">
        
        <span class="tip"></span>
        <van-button v-if="!editing" @click="editing=true" size="mini" type="info" plain></van-button>
        <van-button v-else @click="editing=false" size="mini" type="danger" plain></van-button>
      </div>
      <van-grid class="van-hairline--left">
        <van-grid-item v-for="(item,i) in channelsArr" :key="item.id">
          <span class="f12" :class="{red:activeIndex===i}" @click="enterChannel(i)">{{item.name}}</span>
          <van-icon v-show="editing&&i!=0" class="btn" name="cross" @click="delChannel(i,item.id)"></van-icon>
        </van-grid-item>
      </van-grid>
    </div>
    <div class="channel">
      <div class="tit"></div>
      <van-grid class="van-hairline--left">
        <van-grid-item v-for="item in optionalChannels" :key="item.id">
          <span class="f12">{{item.name}}</span>
          <van-icon class="btn" name="plus" @click="addChannel(item)"></van-icon>
        </van-grid-item>
      </van-grid>
    </div>
  </van-action-sheet>
</template>

<script>
import { getAllChannels, delChannel, addChannel } from '@/api/channels'
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    channelsArr: {
      type: Array,
      // 
      default: () => []
    },
    // 
    activeIndex: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      editing: false,
      // 
      allChannels: []
    }
  },
  methods: {
    // 
    async addChannel ({ id, name }) {
      try {
        // seq
        const orderChannels = this.channelsArr.map((item, i) => {
          return {
            id: item.id,
            name: item.name,
            seq: i
          }
        })
        // 
        orderChannels.push({ id, name, seq: orderChannels.length })
        // 
        orderChannels.splice(0, 1)
        // 
        await addChannel(orderChannels)
        // 
        this.$toast.success('')
        //
        this.channelsArr.push({
          id: id,
          name: name,
          // 
          timestamp: Date.now(),
          articleList: [],
          // v-modelload
          loading: false,
          // finishedload
          finished: false,
          // 
          isLoading: false,
          scrollTop: 0
        })
      } catch (error) {
        this.$toast.fail('')
      }
    },
    //   
    async getAllChannels () {
      const data = await getAllChannels()
      this.allChannels = data.channels
    },
    // 
    enterChannel (i) {
      // index
      //  activeIndexindex

      // Home.vue-----------------------
      // this.$emit('update:')  .sync 
      // this.$emit('update:')
      this.$emit('update:activeIndex', i)
      //   
      this.$emit('input', false)
    },
    // 
    async delChannel (index, channelId) {
      try {
        await delChannel(channelId)
        // 2. 
        this.$toast.success('')
        // 3. 
        if (index <= this.activeIndex) {
          // 
          this.$emit('update:activeIndex', this.activeIndex - 1)
        }
        console.log(this.activeIndex)

        // 4. myChannelsindex
        // 4.1   props  
        // 4.2 props
        // 
        this.channelsArr.splice(index, 1)
        // 5. changeChannel(channel
        //  )
        this.$emit('on-delete')
      } catch (error) {
        this.$toast.fail('')
      }
    }
  },
  computed: {
    //   
    optionalChannels () {
      return this.allChannels.filter(
        item =>
          this.channelsArr.findIndex(myitem => myitem.id === item.id) === -1
      )
    }
  },
  created () {
    this.getAllChannels()
  }
}
</script>

<style lang="less" scoped>
.van-popup--bottom {
  &.van-popup--round {
    border-radius: 0;
  }
}
.van-action-sheet {
  max-height: 100%;
  height: 100%;
  .van-action-sheet__header {
    background: #3296fa;
    color: #fff;
    .van-icon-close {
      color: #fff;
    }
  }
}
.channel {
  padding: 10px;
  .tit {
    line-height: 3;
    .tip {
      font-size: 10px;
      color: #999;
    }
  }
  .van-button {
    float: right;
    margin-top: 7px;
  }
  .btn {
    position: absolute;
    bottom: 0;
    right: 0;
    background: #ddd;
    font-size: 12px;
    color: #fff;
  }
  .f12 {
    font-size: 12px;
    color: #555;
  }
  .red {
    color: red;
  }
}
</style>
