<template>
  <!--    -->
  <!-- vantv-model
  propsv-model-->
  <van-popup :value="value" @input="$emit('input',$event)" @open="isReport=false">
    <van-cell-group v-if="!isReport">
      <van-cell @click="dislikes"></van-cell>
      <van-cell is-link @click="isReport=true"></van-cell>
      <van-cell></van-cell>
    </van-cell-group>
    <van-cell-group v-else>
      <van-cell icon="arrow-left" @click="isReport=false"></van-cell>
      <van-cell v-for="item in reports" :key="item.value" @click="report(item.value)">{{item.label}}</van-cell>
    </van-cell-group>
  </van-popup>
</template>

<script>
import { disLikes, report } from '@/api/articles'
import { reports } from '@/api/constants'
export default {
  props: {
    // 
    value: {
      type: Boolean,
      default: false
    },
    // id
    articleId: {
      type: String,
      default: null
    }
  },
  data () {
    return {
      isReport: false,
      reports
    }
  },
  methods: {
    // 
    async dislikes () {
      try {
        await disLikes(this.articleId)
        // 
        this.$emit('input', false)
        // id
        this.$emit('on-dislikes')
        this.$toast.success('')
      } catch (error) {
        this.$toast.fail('')
      }
    },
    // 
    async report (type) {
      try {
        await report(this.articleId, type)
        this.$emit('input', false)
        this.$emit('on-report')
        this.$toast.success('')
      } catch (error) {
        // 409 
        if (error.response && error.response.status === 409) {
          return this.$toast.fail('')
        }
        this.$toast.fail('')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.van-popup {
  width: 80%;
  border-radius: 4px;
}
</style>
