<template>
  <div class="container">
    <van-tabs v-model="activeIndex" swipeable :lazy-render="false" @change="changeTab">
      <van-tab v-for="item in channelsArr" :title="item.name" :key="item.id">
        <!--  -->
        <div class="scroll-wrapper" @scroll="remember($event)" ref="scollContainer">
          <van-pull-refresh
            v-model="activeChannel.isLoading"
            @refresh="onRefresh"
            :success-text="refreshSuccessText"
          >
            <van-list
              v-model="activeChannel.loading"
              :finished="activeChannel.finished"
              finished-text=""
              @load="onLoad"
            >
              <van-cell
                :to="{name:'article',params:{id:item.art_id.toString()}}"
                v-for="(item,i) in activeChannel.articleList"
                :key="i"
              >
                <!-- <div style="height:100%;">1</div> -->

                <div class="article_item">
                  <h3 class="van-ellipsis">{{item.title}}</h3>
                  <div class="img_box" v-if="item.cover.type===1">
                    <!-- lazy-loadvan-imagev-lazyimagelazy-laod-->
                    <van-image class="w100" fit="cover" :src="item.cover.images[0]" lazy-load />
                  </div>
                  <div class="img_box" v-if="item.cover.type===3">
                    <van-image
                      v-for="(item,i) in item.cover.images"
                      :key="i"
                      class="w33"
                      fit="cover"
                      :src="item"
                      lazy-load
                    />
                  </div>
                  <div class="info_box">
                    <span>{{item.aut_name}}</span>
                    <span>{{item.comm_count}}</span>
                    <span>{{item.pubdate|relTime}}</span>
                    <span
                      class="close"
                      v-if="user.token"
                      @click.stop="openMoreAction(item.art_id.toString())"
                    >
                      <van-icon name="cross"></van-icon>
                    </span>
                  </div>
                </div>
              </van-cell>
            </van-list>
          </van-pull-refresh>
        </div>
        <!-- <van-list v-model="loading" :finished="finished" finished-text="" @load="onLoad">
          <van-cell v-for="item in list" :key="item" :title="item" />
        </van-list>-->
      </van-tab>
    </van-tabs>
    <!--  -->
    <span slot="nav-right" class="bar_btn" @click="openChannelEdit">
      <van-icon name="wap-nav" />
    </span>
    <!-- v-modelv-bind:value='show'
    @input='this.show=$event.target.value'
    componentMoreAction.vuev-model
    v-model
     <van-popup :value="value" @input="$emit('input',$event)">
       
       1.propsv-bindvalue
       2.$emit('input',$event),v-model@input='this.show=$event.target.value'input
       3.valueinputvantv-model
    -->
    <more-action
      v-if="user.token"
      v-model="show"
      :articleId="articleId"
      @on-dislikes="removeArticle()"
      @on-report="removeArticle()"
      @on-delete="changeTab"
    ></more-action>
    <!--
       :activeIndex.sync="activeIndex"
      :activeIndex="activeIndex"
    @update="activeIndex=$event"
    .sync
    v-modelcomponents/channel-edit.vue-->
    <channel-edit
      v-model="showChannelEdit"
      :channelsArr="channelsArr"
      :activeIndex.sync="activeIndex"
      @on-delete="getChannels"
    ></channel-edit>
  </div>
</template>

<script>
import { getChannels } from '@/api/channels'
import { getArticles } from '@/api/articles'
import { mapState } from 'vuex'
import MoreAction from './components/MoreAction'
import ChannelEdit from './components/channel-edit'
export default {
  name: 'home-index',
  components: { MoreAction, ChannelEdit },
  data () {
    return {
      // 
      channelsArr: [],
      // tabid()
      activeIndex: 0,
      // 
      refreshSuccessText: '',
      // // 
      // timestamp: Date.now(),
      // articleList: [],
      // // v-modelload
      // loading: false,
      // // finishedload
      // finished: false,
      // // 
      // count: 0,
      // isLoading: false
      // 
      show: false,
      // id
      articleId: null,
      // 
      showChannelEdit: false
    }
  },
  computed: {
    // 
    // tab
    activeChannel () {
      return this.channelsArr[this.activeIndex]
    },
    // mapStatecomputed
    ...mapState(['user'])
  },
  // 
  //  vuex 
  // 
  watch: {
    user () {
      this.activeIndex = 0
      this.getChannels()
      this.onLoad()
    }
  },
  methods: {
    // 
    openChannelEdit () {
      this.showChannelEdit = true
    },
    // 
    openMoreAction (id) {
      this.show = true
      this.articleId = id
    },
    // 
    removeArticle () {
      const index = this.activeChannel.articleList.findIndex(item => {
        return item.art_id.toString() === this.articleId
      })
      this.activeChannel.articleList.splice(index, 1)
    },
    // load offset ()
    // @load
    // @load
    // @load
    async onLoad () {
      // 
      // 
      // console.log(this.activeIndex)
      // console.log('onload' + this.activeChannel.timestamp)
      // console.log(this.activeChannel.loading)
      // console.log(
      //   this.activeChannel.timestamp + '----------' + this.activeChannel.id
      // )
      await this.$sleep()
      const data = await getArticles({
        channelId: this.activeChannel.id,
        timestamp: this.activeChannel.timestamp
      })
      // console.log(data)
      this.activeChannel.loading = false
      this.activeChannel.articleList.push(...data.results)
      // 
      // console.log(this.activeChannel.loading)
      // 
      if (!data.pre_timestamp) {
        this.activeChannel.finished = true
      } else {
        this.activeChannel.timestamp = data.pre_timestamp
      }
    },
    async onRefresh () {
      this.activeChannel.timestamp = Date.now()
      const data = await getArticles({
        channelId: this.activeChannel.id,
        timestamp: this.activeChannel.timestamp
      })
      this.activeChannel.isLoading = false
      // console.log(data)
      if (data.results.length) {
        this.activeChannel.articleList = data.results
        this.refreshSuccessText = ''

        // 
        this.activeChannel.timestamp = data.pre_timestamp

        // 
        // (10)
        this.onLoad()
      } else {
        this.refreshSuccessText = ''
      }
    },
    // 
    async getChannels () {
      // console.log('-----' + this.user.token)

      const data = await getChannels()
      console.log(data)
      // 
      this.channelsArr = []
      // this.channelsArr = data.channels
      // map() 
      // map()
      // :map() 
      // map() 

      // 
      // timestamp
      // 

      // this.$nextTick(() => {
      this.channelsArr = data.channels.map(item => {
        return {
          id: item.id,
          name: item.name,
          // 
          timestamp: Date.now(),
          articleList: [],
          // v-modelload
          loading: false,
          // finishedload
          finished: false,
          // 
          isLoading: false,
          scrollTop: 0
        }
      })
      // })
    },
    // tabdom
    // scrollTop 
    // tab
    // 
    //  (tab)
    // vanttab

    // tab
    changeTab () {
      if (!this.activeChannel.articleList.length) {
        // 
        this.activeChannel.loading = true
        this.onLoad()
      } else {
        // tab
        // $nextTick
        this.$nextTick(() => {
          const dom = this.$refs['scollContainer'][this.activeIndex]
          dom.scrollTop = this.activeChannel.scrollTop
        })
      }
    },
    // 
    remember (event) {
      this.activeChannel.scrollTop = event.target.scrollTop
      // console.log(this.activeChannel.scrollTop)
    }
  },
  // 
  activated () {
    if (this.$refs['scollContainer']) {
      const dom = this.$refs['scollContainer'][this.activeIndex]
      dom.scrollTop = this.activeChannel.scrollTop
      // console.log(dom.scrollTop)
    }
  },
  created () {
    // this.onLoad()
    this.getChannels()
  }
}
</script>

<style lang="less" scoped>
.van-tabs {
  height: 100%;
  display: flex;
  flex-direction: column;
  margin-bottom: 35px;
  /deep/ .van-tabs__wrap {
    height: 36px;
    padding-right: 36px;
    .van-tab {
      line-height: 36px;
    }
    .van-tabs__line {
      background-color: #3296fa;
      height: 2px;
    }
  }
  /deep/ .van-tabs__content {
    flex: 1;
    overflow: hidden;
  }
  /deep/ .van-tab__pane {
    height: 100%;
    .scroll-wrapper {
      height: 100%;
      overflow-y: auto;
    }
  }
}
.bar_btn {
  width: 36px;
  height: 35px;
  position: absolute;
  top: 0;
  right: 0;
  &::before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 999;
    box-shadow: 0 0 10px #999;
    transform: scale(1, 0.6);
  }
  .van-icon-wap-nav {
    width: 100%;
    height: 100%;
    background: #fff;
    text-align: center;
    line-height: 35px;
    position: relative;
    z-index: 1000;
    &::before {
      font-size: 20px;
    }
  }
}
.article_item {
  h3 {
    font-weight: normal;
    line-height: 2;
  }
  .img_box {
    display: flex;
    justify-content: space-between;
    .w33 {
      width: 33%;
      height: 90px;
    }
    .w100 {
      width: 100%;
      height: 180px;
    }
  }
  .info_box {
    color: #999;
    line-height: 2;
    position: relative;
    font-size: 12px;
    span {
      padding-right: 10px;
      &.close {
        border: 1px solid #ddd;
        border-radius: 2px;
        line-height: 15px;
        height: 12px;
        width: 16px;
        text-align: center;
        padding-right: 0;
        font-size: 8px;
        position: absolute;
        right: 0;
        top: 7px;
      }
    }
  }
}
</style>
